body {
  background-color: silver !important;
}

.container {
  position: relative;
  display: block;
  max-width: 960px;
  margin: 0 auto;
}

:root {
  --mainFont: "SF Pro", "Arial", sans-serif;
  --Colors-Primary-1: #655BFF;
  --Colors-Primary-2: #FFF;
  --Colors-Primary-3: #30303EFF;
  --Colors-Primary-4: #DBDBE4;
  --Colors-Dark-200: #646482FF;
  --Colors-Dark-300: #8E8EA8FF;
  --Colors-Dark-1000: #E8E8EEFF;
  --Colors-Purple-1000: #F4F4FFFF;
  --Colors-States-Info: #1C70D3FF;
  --Colors-States-Success: #48B55AFF;
  --Colors-States-Warning: #D26C21FF;
  --Colors-States-Danger: #B5213BFF;
  --Radius-and-Spacing-xs: 2px;
  --Radius-and-Spacing-s: 4px;
  --Radius-and-Spacing-s-m: 6px;
  --Radius-and-Spacing-m: 8px;
  --Radius-and-Spacing-m-l: 12px;
  --Radius-and-Spacing-l: 16px;
  --Radius-and-Spacing-xl: 24px;
  --Radius-and-Spacing-Ultra: 500px;
  --Shadow-Level-1: 0px 2px 4px 0px rgba(48, 48, 62, 0.12);
}

html, div, span, applet, object, iframe, h1, h2, h3, h4, h5, h6, p, pre, abbr, acronym, address, big, cite, code, del, dfn, em, img, ins, kbd, s, samp, small, strike, strong, sub, sup, tt, var, b, u, i, center, dl, dt, dd, li, fieldset, form, label, legend, caption, tbody, tfoot, thead, tr, th, td, canvas, embed, output, ruby, summary, time, mark, audio, video, input, textarea {
  margin: 0;
  padding: 0;
  border: 0;
  font: inherit;
  box-sizing: border-box;
  vertical-align: baseline;
}

/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure, footer, header, hgroup, menu, nav, section {
  display: block;
  margin: 0;
  padding: 0;
  border: 0;
  font: inherit;
  box-sizing: border-box;
  vertical-align: baseline;
}

body {
  margin: 0;
  padding: 0;
  line-height: 1;
}

ol, ul {
  list-style: none;
  margin: 0;
  padding: 0;
  border: 0;
  font: inherit;
  box-sizing: border-box;
  vertical-align: baseline;
}

blockquote {
  quotes: none;
  margin: 0;
  padding: 0;
  border: 0;
  font: inherit;
  box-sizing: border-box;
  vertical-align: baseline;
}
blockquote:before, blockquote:after {
  content: "";
  content: none;
}

q {
  quotes: none;
  margin: 0;
  padding: 0;
  border: 0;
  font: inherit;
  box-sizing: border-box;
  vertical-align: baseline;
}
q:before, q:after {
  content: "";
  content: none;
}

button, input, select, span {
  font-family: inherit;
  border: none;
  color: inherit;
}
button:focus, input:focus, select:focus, span:focus {
  outline: 0;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
  width: 100%;
  margin: 0;
  padding: 0;
  border: 0;
  font: inherit;
  box-sizing: border-box;
  vertical-align: baseline;
}

a {
  text-decoration: none;
  color: inherit;
  margin: 0;
  padding: 0;
  border: 0;
  font: inherit;
  box-sizing: border-box;
  vertical-align: baseline;
}

b {
  font-weight: bold;
}

sub {
  font-size: 0.8em;
  vertical-align: sub;
}